@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@500&display=swap);
body {
  --bg: rgb(32, 33, 36); /*rgb(40, 42, 54);*/
  --primary: rgb(207, 208, 208); /*rgb(189, 147, 249);*/
  --secondary: rgb(93, 176, 215); /*rgb(139, 233, 253);*/
  --highlight: rgb(242, 151, 102); /* rgb(242, 121, 170);*/
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:"Fira Code",monospace;padding:0 1rem;background-color:var(--bg);color:var(--primary)}.App header a{text-align:center;text-decoration:none;color:var(--primary)}.App a{text-decoration:none;color:var(--secondary);font-weight:bold}.App a[target=_blank]{color:var(--highlight);font-weight:normal}.App main{-webkit-flex:1 1;flex:1 1}.App footer{text-align:center;color:#666;padding:4rem 0 1rem;font-size:.8rem}@media(min-width: 768px){.App{max-width:768px;margin:auto}.App .content{display:-webkit-flex;display:flex}}@media(min-width: 992px){.App{max-width:992px}}
.Sidenav{text-align:center}.Sidenav ul{list-style:none;padding-left:0}.Sidenav ul li{padding:.3rem 0}.Sidenav ul li a{text-decoration:none}.Sidenav ul li a:hover::before{content:" ["}.Sidenav ul li a:hover::after{content:"] "}.Sidenav ul li a.active-link::before{content:"> "}.Sidenav ul li a.active-link::after{content:" <"}.Sidenav ul li a.active-link:hover::before{content:"> ["}.Sidenav ul li a.active-link:hover::after{content:"] <"}@media(min-width: 768px){.Sidenav{height:100%;min-width:200px}}
